





























































import Vue from "vue";

export default Vue.extend({
  name: "CreateRecipe",
    props:{
        dialog: Boolean,
    },
  methods: {
    close() {
        this.$emit('close-dialog')
    },

    save() {
      this.close();
      this.$emit('item-created')
    },
  },
  data: () => ({
    disabledDialog: false,
  }),
});
