


























































import Vue from "vue";
import CreateRecipe from "./CreateRecipe.vue";

export default Vue.extend({
  name: "Recipes",
  components: {
    CreateRecipe,
  },
  methods: {
    editItem(item: any) {
      this.dialog = true;
    },

    deleteItem(item: any) {
      this.idToRemove = item.id;
      this.dialogDelete = true;
    },

    async deleteItemConfirm() {
      try {
        this.disabledDialogDelete = true;
        await (this as any).$http.delete(`/recipes/${this.idToRemove}`);
        this.idToRemove = undefined;
        this.getData();
      } catch (error) {
        console.log(error);
      } finally {
        this.closeDelete();
        this.disabledDialogDelete = false;
      }
      this.closeDelete();
    },

    close() {
      this.dialog = false;
    },

    closeDelete() {
      this.dialogDelete = false;
    },

    async getData() {
      try {
        const response = await (this as any).$http.get("/recipes");
        this.recipes = response.data;
      } catch (error) {
        console.log(error);
      }
    },
  },
  created() {
    this.getData();
  },
  data: () => ({
    idToRemove: undefined,
    disabledDialogDelete: false,
    dialogDelete: false,
    dialog: false,
    headers: [
      {
        text: "Recipe Id",
        align: "start",
        value: "id",
      },
      { text: "Difficulty", value: "difficulty" },
      { text: "Description", value: "description" },
      { text: "Image", value: "image" },
      { text: "Title", value: "title" },
      { text: "Subtitle", value: "subtitle" },
      { text: "Cook time", value: "cook_time" },
      { text: "Tags", value: "tags" },
      { text: "Utensils", value: "utensils" },
      { text: "Allergens", value: "allergens" },
      { text: "Actions", value: "actions", sortable: false },
    ],
    recipes: [],
  }),
});
